import React, { Component } from 'react'
import { withTranslation } from 'react-i18next';

export class NApi extends Component {

  constructor() {
    super()

    this.state = {
      items: [],
      isLoaded: false
    }
  }

  componentDidMount() {
    const url = 'https://api.nivesh.com/API/GetSummaryDetails';
    const postBody = {
      TotalClient: "",
      TotalPartner: "",
      TotalPartnerOverCities: "",
      NoOfTransaction: "",
      TotalTransactionValue: ""

    };
    const requestMetadata = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'token': '636F8F63-06C4-4D95-8562-392B34025FB0'
      },
      body: JSON.stringify(postBody)
    };

    fetch(url, requestMetadata)
      .then(response => response.json())
      .then(data => {
        this.setState({ isLoaded: true, items: data.SummaryDetailsList });
      }).catch(error => {
        console.log(error);
      });
  }

  render() {
    const { isLoaded, items } = this.state;
    const{t}=this.props;
    if (!isLoaded) return <div>Loading...</div>;
    return (
      <div id="about-stats">
        <div className="container container-wrapper">

          {items && (
            <div className="row row justify-content-center align-items-center">
              <div className="col-lg-3 col-md-6 col-12 mb-4">
                <div className="card-box">
                  <h2 className='text-white'>{(items.TotalPartner).toLocaleString('en-IN', {
                    maximumFractionDigits: 0
                  })}</h2>
                  <h4 className='text-white'>{t("Home:hOME61")} <span>{(items.TotalPartnerOverCities).toLocaleString('en-IN', {
                    maximumFractionDigits: 0
                  })}</span> {t("Home:hOME61-A")}</h4>
                </div>
              </div>



              <div className="col-lg-3 col-md-6 col-12 mb-4">
                <div className="card-box">
                  <h2 className='text-white'>{(items.TotalClient).toLocaleString('en-IN', {
                    maximumFractionDigits: 0
                  })}</h2>
                  <h4 className='text-white'>{t("Home:hOME62")}</h4>
                </div>
              </div>

              <div className="col-lg-3 col-md-6 col-12 mb-4">
                <div className="card-box">
                  <h2 className='text-white'>{(items.NoOfTransaction).toLocaleString('en-IN', {
                    maximumFractionDigits: 0
                  })}</h2>
                  <h4 className='text-white'>{t("Home:hOME63")}</h4>
                </div>
              </div>

              <div className="col-lg-3 col-md-6 col-12 mb-4">
                <div className="card-box">
                  <h2 className='text-white'>Rs. {parseInt(items.TotalTransactionValue).toLocaleString('en-IN', {
                    maximumFractionDigits: 0
                  })}</h2>
                  <h4 className='text-white'>{t("Home:hOME64")}</h4>
                </div>
              </div>
            </div>

          )}

        </div>
      </div>


    )
  }
}

export default withTranslation()(NApi)
